*{
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
  background-color: azure;
}

input{
  font-size: 1.2rem;
  width: 100%;
  color: gray;
}

.card-grid{
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  box-shadow: 0px 0px 2px 0px;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  width: 100%;
}